import {mapState} from 'vuex';
import appVideo from "../../components/page-component/app-video/app-video.vue";
import tabBar from './../../core/tabbar.js';

let page = 1;
let is_no_more = false;
let is_loading = false;
export default {
    name: "video",
    components: {
        'app-video': appVideo,
    },
    data() {
        return {
            list: [],
            active: -1,
            navigationBarTitle: '',
        };
    },
    onLoad() { this.$commonLoad.onload();
        page = 1;
        is_no_more = false;
        is_loading = false;
        this.loadData();

        wx.showShareMenu({
            menus: ['shareAppMessage', 'shareTimeline']
        });

       this.$nextTick(() => {
           let currentRoute = this.$platDiff.route();
           tabBar.setNavigationBarTitle(this.bar_title, currentRoute).then(res => {
                this.navigationBarTitle = res;
           });
       });
    },
    onPageScroll(e) {
        this.$store.dispatch('page/actionSetScrollTop', e.scrollTop);
    },
    onReachBottom() {
        if (is_no_more) return;
        this.loadData();
    },

    onShareAppMessage() {
        return this.$shareAppMessage({
            title: this.navigationBarTitle,
            path: '/pages/video/video'
        });
    },


    onShareTimeline() {
        // 分享朋友圈beta
        return this.$shareTimeline({
            title: this.navigationBarTitle,
        });
    },

    computed: {
       ...mapState('mallConfig', {
           bar_title: state => state.bar_title,
       })
    },
    methods: {
        loadData() {
            if (is_loading) {
                return;
            }
            this.$showLoading();
            is_loading = true;
            this.$request({
                url: this.$api.video.index,
                data: {
                    page: page
                }
            }).then(response => {
                this.$hideLoading();
                if (response.code === 0) {
                    this.list = this.list.concat(response.data.list);
                    if (response.data.list.length === 0) {
                        is_no_more = true;
                    }
                    page++;
                }
                is_loading = false;
            }).catch(() => {
                this.$hideLoading();
                is_loading = false;
            })
        },
        click(index) {
            if (this.active === index) {
                this.active = -1;
            } else {
                this.active = index;
            }
        }
    },
}
